import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';

const initialState = {};

@observer
class Home extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
		this.loadAll();	
    }

	async loadAll() {
		const { appState, userStore } = this.props.stores;
        appState.getFingerprint();
        await userStore.getInfo();
    }

	render() {
		const { userStore } = this.props.stores;
		const { user } = userStore;
		return (
			<div class='container-fluid mt-5 pt-2'>
				<div class='row'>
					<div class='col-12'>
						<h1>Home</h1>

						<div class='d-flex'>
							<div class='p-2 flex-grow-1 bg-light mr-2' style='height: 40vh;'>Production / Wear</div>
							<div class='p-2 bg-light' style='height: 40vh;'>Wear status</div>
						</div>

						<div class='d-flex mt-2'>
							<div class='p-2 flex-fill bg-light mr-2' style='height: 40vh;'>Weather</div>
							<div class='p-2 flex-fill bg-light mr-2' style='height: 40vh;'>Electricity prices</div>
							<div class='p-2 flex-fill bg-light' style='height: 40vh;'>Demand</div>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default Home;
